.main-products {
  &__container {
    padding-top: 75px;
    padding-bottom: 30px;

    @include md {
      padding-bottom: 0;
    }
  }

  &__nav {
    display: flex;
    justify-content: center;
    margin-bottom: 45px;
  }

  &__item {
    margin: 0 18px;

    @include xs {
     margin: 0 10px;
    }
  }

  &__btn {
    font-size: 24px;
    &--current {
      &::before {
        width: 100%;
      }
    }

    @include sm {
      font-size: 20px;
    }

  }

  &__btn-center {
    text-align: center;
  }

  &__more {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    margin-top: 92px;
    color: var(--color-gray);
    font-size: 13px;
    transition: transform 0.3s;

    &:hover {
      svg {
        transform: translateY(7px);
        transition: transform 0.3s;
      }
    }

    span {
      display: inline-block;
      margin-bottom: 11px;
    }

    svg {
      width: 12px;
      height: 21px;
      fill: var(--color-dark);
    }

    @include md {
      margin-top: 0;
    }
  }
}

.products-grid {
  display: flex;
  flex-wrap: wrap;

  &__item {
    margin-bottom: 80px;
    width: 18.5%;

    &:not(:nth-child(5n)) {
      margin-right: 1.8%;

      @include md {
        margin-right: 5%;
      }

      @include xs {
        margin-right: 4%;
      }
    }
    &:nth-last-child(-n+5) {
      margin-bottom: 0;

      @include md {
        margin-bottom: 50px;
      }

      @include xs {
        margin-bottom: 30px;
      }
    }

    &:nth-child(3n) {
      @include md {
        margin-right: 0;
      }

      @include xs {
        margin-right: 4%;
      }
    }

    &:nth-child(2n) {
      @include xs {
        margin-right: 0;
      }
    }

    @include md {
      width: 30%;
    }

    @include xs {
      width: 48%;
      margin-right: 4%;
    }
  }
}
