.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 100;
    &__container {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-top: 36px;
      padding-bottom: 36px;

      @include xs {
        padding: 20px 15px;
      }
    }

    &__shop-nav {
      margin-left: 250px;

      @include md{
        margin-left: 0;
      }
    }
}

.logo {
    display: inline-block;
    width: 117px;
    height: 22px;
    svg {
        display: block;
        width: 100%;
        height: 100%;
        fill: var(--color-dark);
        object-fit: cover;
    }

    @include xs {
      width: 90px;
      height: 18px;
    }
}

.catalog-header {
  padding-top: 38px;
}

.free-delivery {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 120;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-white);
  font-size: 10px;
  font-weight: 500;
  text-transform: uppercase;
  background-color: #111;
  height: 40px;

  &__btn {
    position: absolute;
    right: 40px;
    top: 50%;
    width: 12px;
    height: 12px;
    transform: translateY(-50%);

    svg {
      display: block;
      width: 100%;
      height: 100%;
      fill: var(--color-white);
    }
  }
}


.card-page {
  .header {
    padding-top: 0;
  }
}

.nav {
  @include md {
    display: none;
  }
}

.burger {
  position: relative;
  display: none;
  width: 25px;
  height: 18px;

  &::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 2px;
    background-color: var(--color-dark);
  }
  &::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 2px;
    background-color: var(--color-dark);
  }
  &__line {
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    height: 2px;
    transform: translateY(-50%);
    background-color: var(--color-dark);
  }

  @include md {
    display: block;

  }
}
